import { Directive, Input, forwardRef } from '@angular/core';
import { BaseValidator } from './BaseValidator';
import { Validator, AbstractControl, ValidationErrors, NG_VALIDATORS } from '@angular/forms';
// @Directive(BaseValidator.getDirectiveConfig(RequiredValidatorDirective, RequiredValidatorDirective.NAME))
@Directive({
  selector: `[tiRequired][formControlName],[tiRequired][formControl],[tiRequired][ngModel]`,
  providers: [{
      provide: NG_VALIDATORS,
      // tslint:disable-next-line: no-forward-ref
      useExisting: forwardRef(() => RequiredValidatorDirective),
      multi: true
  }]
})
export class RequiredValidatorDirective extends BaseValidator {
  static readonly NAME: string = 'tiRequired';
  @Input(RequiredValidatorDirective.NAME) enabled: boolean;
  validatorStr: string = BaseValidator.getValidatorStr(RequiredValidatorDirective.NAME);
}
