import { Directive, Input, forwardRef } from '@angular/core';
import { BaseValidator } from './BaseValidator';
import { Validator, AbstractControl, ValidationErrors, NG_VALIDATORS } from '@angular/forms';
// @Directive(BaseValidator.getDirectiveConfig(PasswordValidatorDirective, PasswordValidatorDirective.NAME))
@Directive({
  selector: `[tiPassword][formControlName],[tiPassword][formControl],[tiPassword][ngModel]`,
  providers: [{
      provide: NG_VALIDATORS,
      // tslint:disable-next-line: no-forward-ref
      useExisting: forwardRef(() => PasswordValidatorDirective),
      multi: true
  }]
})
export class PasswordValidatorDirective extends BaseValidator {
  static readonly NAME: string = 'tiPassword';
  @Input(PasswordValidatorDirective.NAME) baseValue: any;
  validatorStr: string = BaseValidator.getValidatorStr(PasswordValidatorDirective.NAME);
}
