import { Directive, Input, forwardRef } from '@angular/core';
import { BaseValidator } from './BaseValidator';
import { Validator, AbstractControl, ValidationErrors, NG_VALIDATORS } from '@angular/forms';
// @Directive(BaseValidator.getDirectiveConfig(MaxLengthValidatorDirective, MaxLengthValidatorDirective.NAME))
@Directive({
  selector: `[tiMaxLength][formControlName],[tiMaxLength][formControl],[tiMaxLength][ngModel]`,
  providers: [{
      provide: NG_VALIDATORS,
      // tslint:disable-next-line: no-forward-ref
      useExisting: forwardRef(() => MaxLengthValidatorDirective),
      multi: true
  }]
})
export class MaxLengthValidatorDirective extends BaseValidator {
  static readonly NAME: string = 'tiMaxLength';
  @Input(MaxLengthValidatorDirective.NAME) baseValue: any;
  validatorStr: string = BaseValidator.getValidatorStr(MaxLengthValidatorDirective.NAME);
}
