import { Pipe, PipeTransform } from '@angular/core';
import { TiLocale } from './TiLocale';

/**
 * @ignore
 */
@Pipe({
  name: 'tiTranslate'
})
export class TiTranslatePipe implements PipeTransform {
  transform(keyValue: string, params?: Array<any>): string {
    return TiLocale.translate(keyValue, params);
  }
}
