import { Directive, Input, forwardRef } from '@angular/core';
import { BaseValidator } from './BaseValidator';
import { Validator, AbstractControl, ValidationErrors, NG_VALIDATORS } from '@angular/forms';
// @Directive(BaseValidator.getDirectiveConfig(NotEqualValidatorDirective, NotEqualValidatorDirective.NAME))
@Directive({
  selector: `[tiNotEqual][formControlName],[tiNotEqual][formControl],[tiNotEqual][ngModel]`,
  providers: [{
      provide: NG_VALIDATORS,
      // tslint:disable-next-line: no-forward-ref
      useExisting: forwardRef(() => NotEqualValidatorDirective),
      multi: true
  }]
})
export class NotEqualValidatorDirective extends BaseValidator {
  static readonly NAME: string = 'tiNotEqual';
  @Input(NotEqualValidatorDirective.NAME) baseValue: any;
  validatorStr: string = BaseValidator.getValidatorStr(NotEqualValidatorDirective.NAME);
}
