import {
    Component,
    ElementRef,
    EventEmitter,
    NgZone,
    Renderer2,
    SimpleChanges
} from '@angular/core';
import { TiDateUtil, TiKeymap, Util } from '../../utils/Util';
import { TiLocaleFormat, TiLocale } from '../../locale/TiLocaleModule';
import { TiFormComponent } from '../base/TiBaseModule';
import { TiDateBaseComponent, TiDateCustomizeOptions, TiDatetimeFormat, TiDateValue, TimeOptions } from '../datebase/TiDateBaseModule';
/**
 * DatetimeRange日期时间范围组件
 *
 * Datetime组件提供了一种方便的显示和设置日期时间范围的方式。
 *
 * <example-url>../tiny3demo/#/datetimerange/datetimerange-all</example-url>
 */
@Component({
   selector: 'ti-datetime-range',
   templateUrl: './datetimerange.html',
   styleUrls: ['./datetimerange.less'],
   host: {
    '[class.ti3-datetime-range-input-container]': 'true',
    '(blur)': 'hidePanel()'
},
   providers: [ TiFormComponent.getValueAccessor(TiDatetimeRangeComponent) ]
})

export class TiDatetimeRangeComponent extends TiDateBaseComponent {

    /**
     * @ignore
     * 保存model值
     */
    public oldModel: TiDateValue = {
        begin: null,
        end: null,
    };
    /**
     * @ignore
     * 对应Time组件的接口属性
     */
    public beginTime: TimeOptions = {};
    /**
     * @ignore
     * 对应Time组件的接口属性
     */
    public endTime: TimeOptions = {};
    /**
     * @ignore
     */
    public beginTimeDisabled: boolean;
    /**
     * @ignore
     */
    public endTimeDisabled: boolean;
    /**
     * @ignore
     * 时间日期的format
     */
    public dominatorFormat: string;
    /**
     * @ignore
     * 日期显示格式: Datetime组件的format为DatetimeFormat类型
     */
    public format: TiDatetimeFormat;
    /**
     * @ignore
     * 标记date/datetime的类型
     */
    public isDatetime: boolean = true;
    /**
     * @ignore
     * 用于标记是不是range
     */
    public isRange: boolean = true;
    /**
     * @ignore
     */
    public oldBegintimeValue: any = '';
    /**
     * @ignore
     */
    public oldEndtimeValue: any = '';
    /**
     * @ignore
     * placeholder提示文本
     */
    public placeholder: string = `${TiLocale.getLocaleWords().tiDate.rangeBeginLabel} ─ ${TiLocale.getLocaleWords().tiDate.rangeEndLabel}`;


    ngOnInit(): void {
        super.ngOnInit();
        // 将日期与时间的format拼接为一个字符串
        this.dominatorFormat = `${this.format.date} ${this.format.time}`;
        this.oldFormat = this.format;
        // model有值时，需要记录初始时间值
        if (Util.isUndefined(this.model)) {
            return;
        }
        if (this.model !== null) {
            setTimeout((): void => {
                this.oldBegintimeValue = TiDateUtil.getTimeStr(this.model.begin);
                this.oldEndtimeValue = TiDateUtil.getTimeStr(this.model.end);
            }, 0);
        }
    }
    ngOnChanges(changes: SimpleChanges): void {
        // 验证最大值最小值，为了处理最大值和最小值从合法日期变为undefined 的情景
        if ((changes['max'] && !changes['max'].firstChange) || (changes['min'] && !changes['min'].firstChange)) {
            this.validateMinAndMax(this.config, this.isDatetime);
        }
    }

    ngDoCheck(): void {
        if (!Util.isUndefined(this.model)) {
            // 监听model值的变化
            this.setModel(this.isDatetime);

            // 监听format变化
            this.setFormat();
        }
    }

    /**
     * @ignore
     * 确认按钮事件处理
     */
    public onOkClick(): void {
        if (this.inValidValue) {
            return;
        }

        const beginDateStr: string = TiDateUtil.getDateStr(this.datePanel['value']['begin']);
        // 中间空格误删
        const beginValue: Date = new Date(`${beginDateStr} ${TiDateUtil.addColon(this.beginTime['value'])}`);

        const endDateStr: string = TiDateUtil.getDateStr(this.datePanel['value']['end']);
        // 中间空格误删
        const endValue: Date = new Date(`${endDateStr} ${TiDateUtil.addColon(this.endTime['value'])}`);

        const newModel: TiDateValue = {
            begin: beginValue,
            end: endValue
        };

        // 新旧值不同更新model值
        if (!this.rangeValueIsEqual(this.model, newModel, this.isDatetime)) {
            this.model = newModel;
        }

        this.hideDrop();

        this.okClick.emit(this.model);
    }

    /**
     * @ignore
     * model值的合法性判断
     */
    public isValidValue(value: TiDateValue): boolean {
        if (value === null) {
            return true;
        }

        if (TiDateUtil.isDate(value['begin']) && TiDateUtil.isDate(value['end']) &&
            value['begin'].getTime() <= value['end'].getTime() &&
            TiDateUtil.isBetweenMaxAndmin(value['begin'], this.min, this.max) &&
            TiDateUtil.isBetweenMaxAndmin(value['end'], this.min, this.max)) {
            return true;
        }

        return false;
    }

    /**
     * @ignore
     * 将value转换成format接口格式的字符串
     */
    public formatValue(): void {
        if (this.model === null || (this.model.begin === null && this.model.end === null)) {
            this.inputValue = '';
        } else {
            // 空格误删
            const format: string = `${this.format.date} ${TiDateUtil.addAmPm(this.format.time)}`;
            const begin: string = TiLocaleFormat.formatDateTime(this.model.begin, format);
            const end: string = TiLocaleFormat.formatDateTime(this.model.end, format);
            // 注意：中划线用的是制表符中的中划线，与正常的中线区分开2017-2-12 - 2018-3-20
            this.inputValue = `${begin} ─ ${end}`;
        }
    }

    /**
     * @ignore
     * 配置时间日期面板接口
     */
    public setPickerDate(): void {
        // 设置datepanel指令接口
        this.setDatePanelOptions();

        // 设置time指令接口
        this.setTimeOptions();

        // 设置OK按钮状态
        this.setOkBtnState();
        this.beginTimeDisabled = this.model === null ? true : false;
        this.endTimeDisabled = this.model === null ? true : false;
        this.timeEditDisabled = this.beginTimeDisabled && this.endTimeDisabled;
    }

    // 设置datepanel指令接口
    private setDatePanelOptions(): void {
        let value: TiDateValue;
        if (this.model === null) {
             value = {
                begin: null,
                end: null
             };
        } else {
            value = {
                begin: this.model.begin,
                end: this.model.end
            };
        }

        this.datePanel = {
            value,
            max: this.max,
            min: this.min,
            format : this.format,
            picker: 'day',
            select: (): void => {
                const obj: any = {
                    value: this.datePanel.value[this.focusedPosition],
                    focusedPosition: this.focusedPosition
                };
                this.dayClick.emit(obj);
                // 选择开始日期
                if (this.focusedPosition === 'begin') {
                    if (this.beginTimeDisabled) {
                        this.beginTime['value'] = TiDateUtil.getTimeStr(new Date());
                    }
                    this.beginTimeDisabled = false;
                    this.timeEditDisabled = false;
                    this.dateEditComs.last.focus();
                    this.focusedPosition = 'end';
                } else {
                    if (this.endTimeDisabled) {
                        this.endTime['value'] = TiDateUtil.getTimeStr(new Date());
                    }
                    this.endTimeDisabled = false;
                    if (this.focusedPosition === 'end' && this.datePanel.value.begin === null) {
                        this.focusedPosition = 'begin';
                        this.dateEditComs.first.focus();
                    }
                }
                this.validTime();
                this.setOkBtnState();
            },
            selectTimeFn: (obj: any): void => {
                this.setTimeFn(obj, this.beginTime, this.endTime);
                this.setOkBtnState();
                // 选择结束时分秒 焦点移动到结束时间框
                if (obj.timeOption.includes('end')) {
                    this.textComs.last.nativeElement.focus();
                } else {
                // 选择开始时分秒 焦点移动到开始时间框
                    this.textComs.first.nativeElement.focus();
                }
            }
         };
    }

    private validTime(): void {
        const beginDateStr: string = this.datePanel.value.begin ? TiDateUtil.getDateStr(this.datePanel.value.begin) : '';
        const beginDateTime: Date = new Date(` ${beginDateStr} ${this.beginTime.value}`);
        const endDateStr: string = this.datePanel.value.end ? TiDateUtil.getDateStr(this.datePanel.value.end) : '';
        const endDateTime: Date = new Date(` ${endDateStr} ${this.endTime.value}`);
        if (TiDateUtil.isSmaller(beginDateTime, this.min)) {
            this.beginTime.value = TiDateUtil.getTimeStr(this.min);
        }
        if (TiDateUtil.isBigger(beginDateTime, this.max)) {
            this.beginTime.value = TiDateUtil.getTimeStr(this.max);
        }
        if (TiDateUtil.isBigger(endDateTime, this.max)) {
            this.endTime.value = TiDateUtil.getTimeStr(this.max);
        }
        this.oldBegintimeValue = this.beginTime['value'];
        this.oldEndtimeValue = this.endTime['value'];
    }

    /**
     * @ignore
     *  时间框失焦事件
     */
    public timeBlur(pos: string, val: string): void {
        let timeVal: string = val;
        // 区分中英文冒号
        const reg: RegExp = new RegExp('[\\:\\：]');
        this.beginTime = JSON.parse(JSON.stringify(this.beginTime));
        this.endTime = JSON.parse(JSON.stringify(this.endTime));
        // 输入区间是否存在小数点，非整数
        let isNotInteger: boolean = false;
        const timeArr: Array<string> = val.split(reg);
        timeArr.forEach((item: string, index: number): void => {
            if (item.length === 1) {
                timeArr[index] = '0' + item;
            }
            if (parseInt(item, 10) !== Number(item)) {
                isNotInteger = true;
            }
        });
        timeVal = timeArr.join(':');
        const dateStr: string = '2020/10/30';
        if (pos === 'begin') {
            this.beginTime.value = timeVal;
            const beginDateVal: Date = new Date(`${TiDateUtil.getDateStr(this.datePanel.value.begin)} ${this.beginTime.value}`);
            const isSmallerThanMin: boolean = TiDateUtil.isSmaller(beginDateVal, this.min);
            const beginDate: Date = new Date(`${dateStr} ${TiDateUtil.addColon(this.beginTime['value'])}`);
            const dateVal: Date = new Date(2020, 10, 30, parseInt(timeArr[0], 10), parseInt(timeArr[1], 10), parseInt(timeArr[2], 10));
            if (String(beginDate) === 'Invalid Date' || String(dateVal) === 'Invalid Date' || timeArr.length > 3 || isNotInteger || !this.isBeginSmallerThanEnd() || isSmallerThanMin) {
                this.beginTime.value = this.oldBegintimeValue;
            } else {
                this.oldBegintimeValue = this.beginTime.value;
            }
        } else {
            this.endTime.value = timeVal;
            const endDateVal: Date = new Date(`${TiDateUtil.getDateStr(this.datePanel.value.end)} ${this.endTime.value}`);
            const isBiggerThanMax: boolean = TiDateUtil.isBigger(endDateVal, this.max);
            const endDate: Date = new Date(`${dateStr} ${TiDateUtil.addColon(this.endTime['value'])}`);
            const dateVal: Date = new Date(2020, 10, 30, parseInt(timeArr[0], 10), parseInt(timeArr[1], 10), parseInt(timeArr[2], 10));
            if (String(endDate) === 'Invalid Date' || String(dateVal) === 'Invalid Date' || timeArr.length > 3 || isNotInteger || !this.isBeginSmallerThanEnd() || isBiggerThanMax) {
                this.endTime.value = this.oldEndtimeValue;
            } else {
                this.oldEndtimeValue = this.endTime.value;
            }
        }
        this.setOkBtnState();
    }
    /**
     * @ignore
     * 时间框enter事件
     */
    public timeKeydown(pos: string, val: string, event: KeyboardEvent): void {
        if (event.keyCode === TiKeymap.KEY_ENTER  || event.keyCode === TiKeymap.KEY_NUMPAD_ENTER) {
            this.timeBlur(pos, val);
        }
    }
    /**
     * @ignore
     */
    public dateEditBlur(val: any, pos: string): void {
        if (val === null) {
            return;
        }
        if (TiDateUtil.isBetweenMaxAndmin(val, this.min, this.max)) {
            if (pos === 'begin') {
                const inputValue: string = this.dateEditComs.first.nativeElement.firstChild.value;
                if (inputValue === '') {
                    return;
                }
                const beginDateVal: Date = new Date(`${inputValue} ${this.beginTime['value']}`);
                if (TiDateUtil.isDate(this.datePanel.value.end)) {
                    const endDateVal: Date = new Date(`${TiDateUtil.getDateStr(this.datePanel.value.end)} ${this.endTime['value']}`);
                    if (TiDateUtil.isBigger(beginDateVal, endDateVal)) {
                        this.datePanel.value.begin = this.oldBeginValue || this.oldModel && this.oldModel.begin;
                        return;
                    }
                }
                if (this.beginTimeDisabled) {
                    this.beginTime['value'] = TiDateUtil.getTimeStr(new Date());
                }
                this.oldBeginValue = this.datePanel.value.begin;
                this.beginTimeDisabled = false;
            } else {
                if (this.endTimeDisabled) {
                    this.endTime['value'] = TiDateUtil.getTimeStr(new Date());
                }
                const inputValue: string = this.dateEditComs.last.nativeElement.firstChild.value;
                if (inputValue === '') {
                    return;
                }
                const endDateVal: Date = new Date(`${inputValue} ${this.endTime['value']}`);
                if (TiDateUtil.isDate(this.datePanel.value.begin)) {
                    const beginDateVal: Date = new Date(`${TiDateUtil.getDateStr(this.datePanel.value.begin)} ${this.beginTime['value']}`);
                    if (TiDateUtil.isBigger(beginDateVal, endDateVal)) {
                        this.datePanel.value.end = this.oldEndValue || this.oldModel && this.oldModel.end;
                        return;
                    }
                }
                this.oldEndValue = this.datePanel.value.end;
                this.endTimeDisabled = false;
            }
            this.validTime();
            this.setOkBtnState();
            this.timeEditDisabled = this.beginTimeDisabled && this.endTimeDisabled;
        }

    }
    /**
     * @ignore
     */
    public onKeydownFn(event: KeyboardEvent, val: any, pos: string): void {
        if (event.keyCode === TiKeymap.KEY_ENTER  || event.keyCode === TiKeymap.KEY_NUMPAD_ENTER) {
            this.dateEditBlur(val, pos);
        }
     }

    // 设置time指令接口
    private setTimeOptions(): void {
        // 设置开始time指令接口
        this.setBeginTimeOptions();

        // 设置结束time指令接口
        this.setEndTimeOptions();
    }

    // 设置开始面板time指令接口
    private setBeginTimeOptions(): void {
        // value接口设置
        this.setBeginTimeValue();

        // max接口设置
        this.setBeginTimeMaxValue();

        // min接口设置
        this.setBeginTimeMinValue();
    }

    // time指令的起始面板的value接口设置 点击删除按钮：如果设置nowDateTime并且值为合法时间日期，时间设置为nowDateTime的时间
    private setBeginTimeValue(): void {
        if (this.model === null || this.model['begin'] === null && this.model['end'] === null) {
            this.beginTime['value'] = '';

            return;
        }

        this.beginTime['value'] = TiDateUtil.getTimeStr(this.model.begin);
    }

    /**
     * @ignore
     * time指令的起始面板的max接口设置
     */
    public setBeginTimeMaxValue(): void {
        // 起始日期是最大日期时，时间组件最大值是max接口中的时间；其他情况最大值是"23:59:59"
        const value: TiDateValue = this.datePanel['value'];
        this.beginTime['max'] = ((value instanceof Object) && TiDateUtil.isDateEqual(this.max, value['begin'])) ?
                                TiDateUtil.getTimeStr(this.max) : '23:59:59';
    }

    /**
     * @ignore
     * time指令的起始面板的min接口设置
     */
    public setBeginTimeMinValue(): void {
        // 起始日期是最小日期时，时间组件最小值是min接口中的时间；其他情况最大值是"00:00:00"
        const value: TiDateValue = this.datePanel['value'];
        this.beginTime['min'] = ((value instanceof Object) && TiDateUtil.isDateEqual(this.min, value['begin'])) ?
                                TiDateUtil.getTimeStr(this.min) : '00:00:00';
    }

    // 设置结束面板time指令接口
    private setEndTimeOptions(): void {
        // value接口设置
        this.setEndTimeValue();

        // max接口设置
        this.setEndTimeMaxValue();

        // min接口设置
        this.setEndTimeMinValue();
    }

    // time指令的结束面板的value接口设置
    private setEndTimeValue(): void {
        if (this.model === null || this.model['begin'] === null && this.model['end'] === null) {
            this.endTime['value'] = '';

            return;
        }

        this.endTime['value'] = TiDateUtil.getTimeStr(this.model.end);
    }

    /**
     * @ignore
     * time指令的结束面板的max接口设置
     */
    public setEndTimeMaxValue(): void {
        // 结束日期是最大日期时，时间组件最大值是max接口中的时间; 否则，时间组件最大值是"23:59:59"
        const value: TiDateValue = this.datePanel['value'];
        this.endTime['max'] = ((value instanceof Object) && TiDateUtil.isDateEqual(this.max, value['end'])) ?
                                TiDateUtil.getTimeStr(this.max) : '23:59:59';
    }

    /**
     * @ignore
     * time指令的结束面板的min接口设置
     */
    public setEndTimeMinValue(): void {
        // 起始日期是最小日期时，时间组件最小值是min接口中的时间；其他情况最大值是"00:00:00"
        const value: TiDateValue = this.datePanel['value'];
        this.endTime['min'] = ((value instanceof Object) && TiDateUtil.isDateEqual(this.min, value['end'])) ?
                                TiDateUtil.getTimeStr(this.min) : '00:00:00';
    }

    /**
     * @ignore
     * 设置确认按钮的状态
     */
    public setOkBtnState(): void {
        // 输入中文冒号之后，及时转换为英文冒号
        const inputvalue: string = this.beginTime.value;
        const inputvalue1: string = this.endTime.value;
        this.beginTime.value = inputvalue.replace('：', ':');
        this.endTime.value = inputvalue1.replace('：', ':');
        // 判断下拉面板中时间区间是否合法
        this.inValidValue = !this.isValidRange();
        if (this.buttonComs) {
            this.setAttr(this.buttonComs.last.nativeElement, 'disabled', this.inValidValue);
            this.setInputStyle(this.inValidValue);
        }
    }

    // 判断下拉面板中时间区间是否合法
    private isValidRange(): boolean {
        const date: TiDateValue = this.datePanel.value;

        return (date instanceof Object) && (TiDateUtil.isDate(date.begin)) &&
                (TiDateUtil.isDate(date.end)) && this.isBeginSmallerThanEnd();
    }

    // 判断起始值和结束值都在最小值最大值之间
    private isBeginSmallerThanEnd(): boolean {
        if (!TiDateUtil.isDate(this.datePanel.value.begin) || !TiDateUtil.isDate(this.datePanel.value.end)) {
            return false;
        }
        // 组装起始日期时间 空格误删
        const begin: Date = new Date(
            `${TiDateUtil.getDateStr(this.datePanel.value.begin)} ${TiDateUtil.addColon(this.beginTime['value'])}`);
        const end: Date = new Date(
            `${TiDateUtil.getDateStr(this.datePanel.value.end)} ${TiDateUtil.addColon(this.endTime['value'])}`);

        return begin.getTime() <= end.getTime();
    }
}
