import { Directive, Input, forwardRef } from '@angular/core';
import { BaseValidator } from './BaseValidator';
import { Validator, AbstractControl, ValidationErrors, NG_VALIDATORS } from '@angular/forms';
// @Directive(BaseValidator.getDirectiveConfig(Ipv4ValidatorDirective, Ipv4ValidatorDirective.NAME))
@Directive({
  selector: `[tiIpv4][formControlName],[tiIpv4][formControl],[tiIpv4][ngModel]`,
  providers: [{
      provide: NG_VALIDATORS,
      // tslint:disable-next-line: no-forward-ref
      useExisting: forwardRef(() => Ipv4ValidatorDirective),
      multi: true
  }]
})
export class Ipv4ValidatorDirective extends BaseValidator {
  static readonly NAME: string = 'tiIpv4';
  @Input(Ipv4ValidatorDirective.NAME) enabled: boolean;
  validatorStr: string = BaseValidator.getValidatorStr(Ipv4ValidatorDirective.NAME);
}
