import { Directive, Input, forwardRef } from '@angular/core';
import { BaseValidator } from './BaseValidator';
import { Validator, AbstractControl, ValidationErrors, NG_VALIDATORS } from '@angular/forms';
// @Directive(BaseValidator.getDirectiveConfig(MinValueValidatorDirective, MinValueValidatorDirective.NAME))
@Directive({
  selector: `[tiMinValue][formControlName],[tiMinValue][formControl],[tiMinValue][ngModel]`,
  providers: [{
      provide: NG_VALIDATORS,
      // tslint:disable-next-line: no-forward-ref
      useExisting: forwardRef(() => MinValueValidatorDirective),
      multi: true
  }]
})
export class MinValueValidatorDirective extends BaseValidator {
  static readonly NAME: string = 'tiMinValue';
  @Input(MinValueValidatorDirective.NAME) baseValue: any;
  validatorStr: string = BaseValidator.getValidatorStr(MinValueValidatorDirective.NAME);
}
