import { Directive, ElementRef, Renderer2, NgZone, AfterViewInit, OnDestroy } from '@angular/core';
/**
 * @ignore
 * 点击需要和聚焦区分开。当点击的时候，不需要有outline。
 * 10.1.4新增
 */
@Directive({
    selector: '[tiOutline]'
})
export class TiOutlineDirective implements AfterViewInit {
    constructor(private renderer: Renderer2, public hostEle: ElementRef, private zone: NgZone) {
    }
    ngAfterViewInit(): void {
        this.zone.runOutsideAngular(() => {
            const outlineHostEle: any = this.hostEle.nativeElement;
            if (outlineHostEle) {
                this.renderer.listen(outlineHostEle, 'mousedown', (): void => {
                    this.renderer.setStyle(outlineHostEle, 'outline', 'none');
                });
                this.renderer.listen(outlineHostEle, 'blur', (): void => {
                    this.renderer.setStyle(outlineHostEle, 'outline', '');
                });
            }
        });
    }
 }