import {
    ChangeDetectorRef,
    Component,
    ElementRef,
    Input,
    OnChanges,
    OnDestroy,
    OnInit,
    SimpleChange,
    SimpleChanges
} from '@angular/core';
import { TiFormfieldComponent } from './TiFormfieldComponent';
import { Util } from '../../utils/Util';

/**
 * 本组件嵌在 tiFormfield 中使用，包裹着单个表单条目的所有内容
 */
@Component({
    selector: 'ti-item',
    templateUrl: './formfield-item.html'
})
export class TiItemComponent implements OnInit, OnChanges, OnDestroy {
    /**
     * 设置当前表单条目是否是必填，如果是必填，则前面会显示 *
     */
    @Input() required: boolean = false;
    /**
     * 设置当前表单条目的垂直对齐方式，默认为top（上下布局表单不支持该属性）。
     *
     * 能够覆盖tiFormfield指令上的verticalAlign设置
     */
    @Input() verticalAlign: 'top' | 'middle' | 'bottom';
    /**
     * 设置当前表单条目是否显示
     *
     * tiItem 不支持 ngIf 和 hidden，由该接口来控制单个表单条目的显示与否
     */
    @Input() show: boolean = true;
    /**
     * 行合并，设置当前表单条目可横跨的行数
     */
    @Input() rowspan: number = 1;
    /**
     * 列合并，设置当前表单条目可横跨的列数
     */
    @Input() colspan: number = 1;
    /**
     * 在 ti-formfield 中的展示顺序，数值越大越靠后展示；
     *
     * 要使用该接口时，当前ti-formfield中的所以ti-item都要设置该接口；
     *
     * 不设置时按 ti-item 的解析顺序展示
     *
     * 10.0.1新增
     */
    @Input() index: number;
    /**
     * @ignore
     * tiItem的索引值，用于控制其显示时的位置顺序
     */
    public itemIndex: number;
    /**
     * @ignore
     */
    public verticalAlignObj: { 'vertical-align'?: string };
    /**
     * @ignore
     */
    public itemStyle: { 'grid-area'?: string };
    /**
     * @ignore
     */
    public labelStyle: { 'vertical-align'?: string, width?: string, 'line-height'?: string };
    /**
     * @ignore
     */
    public contentStyle: { 'vertical-align'?: string, 'line-height'?: string, 'width'?: string };
    /**
     * @ignore
     */
    public element: HTMLElement;
    /**
     * @ignore
     */
    public itemLabel: string | Node;
    /**
     * @ignore
     */
    public hasLabel: boolean = false;
    /**
     * @ignore
     */
    public gapWidth: string = '0px';
    constructor(private formfield: TiFormfieldComponent, private elementRef: ElementRef,
                private changeDetector: ChangeDetectorRef) {}

    // 有两处改变label
    // 1. label接口传入的值
    // 2. 在ti-item-label组件中修改label的值
    /**
     * 设置当前表单条目的label文本，支持简单字符串文本，如果开发者希望基于html定制文本，
     * 建议使用[TiItemLabelComponent]{@link ./TiItemLabelComponent.html}组件
     *
     * 类型：string | Node
     */
    @Input()
    set label(labelParm: string | Node) {
        this.setItemLabel(labelParm);
    }

    ngOnInit(): void {
        this.formfield.itemTotal++;
        this.decorateItem();
        if (this.show) {
            this.formfield.addCntItem(this);
        }
    }

    ngOnChanges(changes: SimpleChanges): void {
        const showObj: SimpleChange = changes['show'];
        if (showObj && !showObj.firstChange) {
            if (showObj.currentValue) {
                this.formfield.addCntItem(this);
            } else {
                this.formfield.removeCntItem(this);
            }
        }
    }

    ngOnDestroy(): void {
        // 在外部将ti-iem销毁时，应将此item从items数组中去掉
        this.formfield.removeCntItem(this);
        this.changeDetector.detach();
    }

    private decorateItem(): void {
        this.itemIndex = Util.isNumber(this.index) ? this.index : this.formfield.itemTotal - 1;
        this.element = this.elementRef.nativeElement;
        this.verticalAlignObj = Util.isUndefined(this.verticalAlign) ?
            {} : { 'vertical-align': this.verticalAlign };

        // item-label的自定义的样式（垂直对齐方式、宽度）
        this.labelStyle = {
            'vertical-align': this.verticalAlign,
            width: this.formfield.labelWidth
        };

        // item-content的自定义的样式（垂直对齐方式）
        this.contentStyle = {
            'vertical-align': this.verticalAlign
        };
        // 为纯文本描述类表单时，可设置 item 的行高
        if (this.formfield.type === this.formfield.textType) {
            this.labelStyle['line-height'] = this.formfield.textLineHeight;
            this.contentStyle['line-height'] = this.formfield.textLineHeight;
        }

        // item 自定义行列合并样式
        if (this.formfield.useGrid) {
            this.itemStyle = { 'grid-area': `span ${this.rowspan} / span ${this.colspan}` };
        }
    }

    private setItemLabel(label: string | Node): void {
        if (label) {
            this.hasLabel = true;
            this.itemLabel = label;
        } else {
            this.hasLabel = false;
        }
        this.changeDetector.detectChanges();
    }
}
